import Vue from 'Vue'
import Vuex from 'Vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //  
    audioInfo: [{
      id: 1617440573040,
      url: '/mp3/.mp3',
      cover: '/images/.png',
      lrc:
        "[00:00.000]  : \n[00:01.000]  : \n[00:02.000] : Beyond\n[00:03.000] : Beyond / Gordon O'Yang\n[00:04.000]Synth Programming : Gordon O'Yang / \n[00:05.000]Mixed by Philip Kwok\n[00:06.000]\n[00:28.650]\n[00:33.036]\n[00:36.281]\n[00:41.564]\n[00:46.038] \n[00:54.543]\n[01:01.056]\n[01:07.551]\n[01:11.162]\n[01:14.391]\n[01:20.515]\n[01:24.167]\n[01:27.343]\n[01:31.091]\n[01:43.174]\n[01:47.643]\n[01:50.774]\n[01:56.103]\n[02:00.572]\n[02:03.764]\n[02:09.060]\n[02:15.509]\n[02:22.001]\n[02:25.740]\n[02:28.901]\n[02:34.992]\n[02:38.655]\n[02:41.900]\n[02:45.698]\n[03:23.643]\n[03:27.339]\n[03:30.561]\n[03:36.626]\n[03:40.315]\n[03:43.493]\n[03:48.026]Woo\n[03:50.494]\n[03:55.305]Ah\n[03:57.239]\n[03:59.359]\n[04:02.934]\n[04:06.174]\n[04:12.267]\n[04:15.855]\n[04:19.047]\n[04:23.624]Woo\n[04:26.040]\n[04:30.932]Ah\n[04:32.610]\n[04:35.027]\n[04:38.580]\n[04:41.808]\n[04:47.928]\n[04:51.555]\n",
      theme: '#f56c6c',
      name: '',
      artist: '',
    },
    {
      id: 1617440751144,
      url: '/mp3/.mp3',
      cover: '/images/.png',
      lrc:
        '[00:00.000]  : \n[00:01.000]  : \n[00:02.000]  : Beyond/Kunihiko Ryo\n[00:03.000]  : Beyond/Kunihiko Ryo\n[00:04.000]\n[00:18.123] \n[00:24.720]\n[00:30.632] \n[00:36.858]\n[00:39.922]\n[00:43.073] \n[00:49.655]\n[00:55.537] \n[01:01.742]\n[01:04.863]\n[01:08.371]\n[01:15.410]\n[01:21.635] \n[01:27.845]\n[01:33.698]\n[01:42.254] \n[01:48.851]\n[01:54.701] \n[02:00.959]\n[02:04.052]\n[02:07.560]\n[02:14.562]\n[02:20.822] \n[02:27.073]\n[02:33.182]\n[03:07.939] \n[03:14.541]\n[03:19.221]\n[03:26.194]\n[03:32.460] \n[03:38.709]\n[03:44.956] \n[03:51.220]\n[03:56.672]\n[04:03.671]\n[04:09.912] \n[04:16.142]\n[04:22.325]\n[04:25.000]OP : Amuse Inc. & Fun House Inc.\n[04:28.000]SP : Amuse H.K. Ltd.\n',
      theme: '#f56c6c',
      name: '',
      artist: '',
    }],
  },
  mutations: {
    // 
    updateAudioInfo(state, payload) {
      // 
      state.audioInfo = state.audioInfo.filter(item => item.id != payload.id)
      state.audioInfo.push(payload)
    },
    // 
    insertAudioInfo(state, payload) {
      // 
      state.audioInfo = state.audioInfo.filter(item => item.id != payload.id)
      // 
      state.audioInfo.unshift(payload)
    },
    // 
    clear(state) {
      state.audioInfo = []
    }
  },
  // Actions  state mutations
  actions: {
    // 
    async getAudioInfo(context, payload) {
      console.log('payload: ', payload);
      // payload:{
      //   id:id,
      //   isInsert:true || false
      // }

      let result = {}

      Promise.all([
        context.dispatch('getMusicUrl', payload.id),
        context.dispatch('getMusicInfo', payload.id),
        context.dispatch('getMusicLyric', payload.id)
      ]).then(res => {
        console.log('res: ', res);
        // id 
        result.id = payload.id
        // 
        result.url = res[0].data.data[0].url || false
        // 
        result.name = res[1].data.songs[0].name || false
        // 
        result.artist = res[1].data.songs[0].ar[0].name || false
        // 
        result.cover = res[1].data.songs[0].al.picUrl || false
        // 
        result.lrc = res[2].data.lrc ? res[2].data.lrc.lyric : null
        // 
        result.theme = '#f56c6c'

        if (payload.isInsert) {
          // 
          context.commit('insertAudioInfo', result)
        } else {
          // 
          context.commit('updateAudioInfo', result)
        }
      }, reason => {
        console.log(': ', reason);
      })

    },
    // Url
    getMusicUrl(context, id) {
      return new Promise((resolve, reject) => {
        axios.get(`/song/url?id=${id}`).then(res => {
          resolve(res)
        }, reason => {
          reject(reason)
        })
      })
    },
    // 
    getMusicInfo(context, id) {
      return new Promise((resolve, reject) => {
        axios.get(`/song/detail?ids=${id}`).then(res => {
          resolve(res)
        }, reason => {
          reject(reason)
        })
      })
    },
    // 
    getMusicLyric(context, id) {
      return new Promise((resolve, reject) => {
        axios.get(`/lyric?id=${id}`).then(res => {
          resolve(res)
        }, reason => {
          reject(reason)
        })
      })
    }
  },
})