<template>
  <div class="top-container">
    <div class="left dflex">
      <img src="~/assets/images/home.svg" class="homeImg" align="middle" alt="" />
      <el-tooltip class="item" :enterable="false" effect="dark" content="www.bookbook.cc" placement="right">
        <a href="https://www.bookbook.cc" target="blank">bookbook.cc</a>
      </el-tooltip>
      <div class="control-go">
        <el-button type="info" size="small" class="clear-icon el-icon-arrow-left" @click="go(-1)" circle></el-button>
        <el-button type="info" size="small" class="clear-icon el-icon-arrow-right" @click="go(1)" circle></el-button>
        <el-button type="info" size="small" class="clear-icon iconfont icon-zhuye2" @click="goHome" circle></el-button>
      </div>
    </div>
    <div class="center">
      <el-autocomplete
        @keyup.native.enter="search"
        v-model="inputValue"
        :fetch-suggestions="getSearchAsync"
        :placeholder="placeholder"
        @select="handleSelect"
        prefix-icon="el-icon-search"
        clearable
        class="border-radius"
        size="small"
      ></el-autocomplete>
    </div>
    <div class="right">
      <el-switch v-model="navState" @change="changeNavState"> </el-switch>
      <el-switch class="music-control-state" v-model="state" @change="changeState"> </el-switch>
      <el-dropdown :hide-on-click="false">
        <span class="el-dropdown-link"> <i class="el-icon-arrow-down el-icon--right"></i> </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="to('/discovery')" icon="iconfont icon-find-music"></el-dropdown-item>
          <el-dropdown-item @click.native="to('/playlists')" icon="iconfont icon-find-music"></el-dropdown-item>
          <el-dropdown-item @click.native="to('/songs')" icon="iconfont icon-music"></el-dropdown-item>
          <el-dropdown-item @click.native="to('/mvs')" icon="iconfont icon-mv">MV</el-dropdown-item>
          <el-dropdown-item divided>
            <a href="https://www.bilibili.com/video/BV1RE411W776" target="_blank"> BiliBili</a>
          </el-dropdown-item>
          <el-dropdown-item>
            <a href="https://gitee.com/penggang-home/cloud-music" target="_blank"> Gitee</a>
          </el-dropdown-item>
          <el-dropdown-item>
            <a href="https://github.com/Binaryify/NeteaseCloudMusicApi" target="_blank"> Github</a>
          </el-dropdown-item>
          <el-dropdown-item >
            <a href="https://neteasecloudmusicapi.vercel.app/#/?id=neteasecloudmusicapi" target="_blank">API</a>
          </el-dropdown-item>
        
          <el-dropdown-item disabled>v1.0.0</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <!-- <el-tooltip class="item" effect="dark" content="~" placement="bottom">
        <el-button type="danger" size="small" round v-popover:popover>
          <i class="iconfont icon-power"></i>
        </el-button>
      </el-tooltip> -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'Top',
  data() {
    return {
      // 
      placeholder: '',
      // 
      inputValue: '',
      // 
      searchHot: [],
      // 
      searchHotList: [],
      // 
      auth: '',
      // 
      searchKeywords: '',
      //   
      state: true,
      //  nav
      navState: true,
    }
  },
  created() {
    // 
    this.getDefaultKeywords()
  },
  computed: {},
  methods: {
    // 
    to(str) {
      if (this.$route.path != str) {
        this.$router.push(str)
      }
    },
    //  or 
    go(index) {
      window.history.go(index)
    },
    // 
    goHome() {
      if (this.$route.path != '/discovery') {
        this.$router.push('/discovery')
      }
    },
    // 
    changeState() {
      this.$Bus.$emit('switchState', this.state)
    },
    changeNavState() {
      this.$Bus.$emit('changeNavState', this.navState)
    },
    // 
    search() {
      //  
      if (this.$route.query.q == this.inputValue.trim()) {
        return
      }

      if (this.inputValue.trim() == '') {
        this.$message('~')
        return
      }

      // 
      if (this.$route.path != '/result') {
        this.$router.push(`/result?q=${this.inputValue}`)
      } else {
        this.$router.replace(`/result?q=${this.inputValue}`)
      }
    },
    // 
    async getDefaultKeywords() {
      const { data: data } = await this.$axios.get('/search/default')
      if (data.code == 200) {
        this.placeholder = data.data.showKeyword
        // 
        // this.inputValue = data.data.realkeyword
      }
    },
    // +
    async getSearchAsync(queryString, cb) {
      if (this.inputValue == '') {
        // 
        if (this.searchHotList != '') return cb(this.searchHotList)
        this.auth = 'system'
        this.searchHotList = []
        // 
        const { data: data } = await this.$axios.get('/search/hot/')
        if (data.code == 200) {
          data.result.hots.forEach(item => {
            this.searchHotList.push({ value: item.first })
          })
          cb(this.searchHotList)
        }
      } else {
        // 
        if (queryString == this.searchKeywords) return cb(this.searchHot)

        this.auth = 'user'
        this.searchHot = []
        // 
        const { data: data } = await this.$axios.get(`/search/suggest?keywords=${queryString}`)
        if (data.code == 200) {
          this.searchKeywords = queryString
          //  songs
          if (data.result.songs && data.result.songs.length > 0) {
            data.result.songs.forEach(item => {
              this.searchHot.push({
                // value: '' + item.name + '' + item.artists[0].name,
                value: item.name + '' + item.artists[0].name,
                id: item.id,
                type: 'songs',
              })
            })
          }
          //   albums
          // if (data.result.albums && data.result.albums.length > 0) {
          //   data.result.albums.forEach(item => {
          //     this.searchHot.push({
          //       value: '' + item.name + '' + item.artist.name,
          //       id: item.id,
          //       type: 'albums',
          //     })
          //   })
          // }
          //  playlists
          // if (data.result.playlists && data.result.playlists.length > 0) {
          //   data.result.playlists.forEach(item => {
          //     this.searchHot.push({
          //       value: '' + item.name + '' + item.name,
          //       id: item.id,
          //       type: 'playlists',
          //     })
          //   })
          // }
          cb(this.searchHot)
        }
      }
    },
    // 
    handleSelect(item) {
      this.search()
    },
  },
}
</script>

<style  scoped>
.top-container {
  padding: 0;
  padding-right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.center {
  flex-basis: 400px;
}
.el-autocomplete {
  width: 100%;
}
.left .homeImg {
  width: 28px;
  height: auto;
  object-fit: cover;
}
.left {
  position: relative;
  flex-basis: 200px;
  text-align: center;
}
.left a {
  padding-left: 5px;
  font-size: 22px;
  color: black;
}
.control-go {
  position: absolute;
  right: -117px;
}
.clear-icon {
  outline: none;
}
.music-control-state {
  transform: rotate(-90deg);
}
.el-dropdown {
  cursor: pointer;
}
.el-dropdown-menu__item {
  user-select: none;
}
a {
  color: black;
}
</style>
