<template>
  <div class="mvs-container">
    <div class="filter-wrap">
      <div class="seciton-wrap">
        <span class="section-type">:</span>
        <ul class="tabs-wrap">
          <li class="tab">
            <span :class="['title', { active: queryInfo.area == '' }]" @click="queryInfo.area = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.area == '' }]" @click="queryInfo.area = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.area == '' }]" @click="queryInfo.area = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.area == '' }]" @click="queryInfo.area = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.area == '' }]" @click="queryInfo.area = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.area == '' }]" @click="queryInfo.area = ''"></span>
          </li>
        </ul>
      </div>
      <div class="type-wrap">
        <span class="type-type">:</span>
        <ul class="tabs-wrap">
          <li class="tab">
            <span :class="['title', { active: queryInfo.type == '' }]" @click="queryInfo.type = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.type == '' }]" @click="queryInfo.type = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.type == '' }]" @click="queryInfo.type = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.type == '' }]" @click="queryInfo.type = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.type == '' }]" @click="queryInfo.type = ''"></span>
          </li>
        </ul>
      </div>
      <div class="order-wrap">
        <span class="order-type">:</span>
        <ul class="tabs-wrap">
          <li class="tab">
            <span :class="['title', { active: queryInfo.order == '' }]" @click="queryInfo.order = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.order == '' }]" @click="queryInfo.order = ''"></span>
          </li>
          <li class="tab">
            <span :class="['title', { active: queryInfo.order == '' }]" @click="queryInfo.order = ''"></span>
          </li>
        </ul>
      </div>
    </div>
    <!-- MV -->
    <div class="mvs">
      <div class="items">
        <div class="item" @click="toMv(item.id)" v-for="(item, index) in mvlist" :key="index">
          <div class="img-wrap">
            <img v-lazy="item.cover" alt="" />
            <div class="num-wrap">
              <div class="iconfont icon-play"></div>
              <div class="num">{{ item.playCount | ellipsisPlayVolume }}</div>
            </div>
          </div>
          <div class="info-wrap">
            <div class="name">{{ item.name }}</div>
            <div class="singer">{{ item.artistName }}</div>
          </div>
        </div>
      </div>
      <!--  -->
      <el-pagination
        :hide-on-single-page="true"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paginationForm.page"
        :page-sizes="[4, 8, 12, 16, 20, 40]"
        :page-size="paginationForm.limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="paginationForm.total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Mvs',
  data() {
    return {
      // 
      paginationForm: {
        // 
        total: 0,
        // 
        page: 0,
        // 
        limit: 8,
      },
      // 
      queryInfo: {
        // `area`: ,
        area: '',
        // `type`: ,,
        type: '',
        // `order`: ,
        order: '',
      },
      // mv
      mvlist: [],
    }
  },
  created() {
    this.getMvs()
  },
  computed: {
    // watch
    // queryInfoArea(){
    //   return this.queryInfo.area
    // }
  },
  watch: {
    // 1.watch
    queryInfo: {
      handler(val) {
        this.paginationForm = {
          total: 0,
          page: 0,
          limit: 8,
        }
        this.queryInfo = val
        this.getMvs()
      },
      deep: true,
    },

    // 2.watch
    // 'queryInfo.area': {
    //   handler: function (val) {
    //     console.log(val);
    //   },
    // },

    // 3.watch 
    // queryInfoArea(val){
    //   console.log(val);
    // }
  },
  // 
  filters: {
    // 
    ellipsisPlayVolume(val) {
      //  xx
      if (val < 100000) return val
      return parseInt(val / 10000) + ''
    },
  },
  methods: {
    toMv(id) {
      this.$router.push(`/mv?id=${id}`)
    },
    // 
    handleCurrentChange(val) {
      this.paginationForm.page = val
      this.getMvs()
    },
    // 
    handleSizeChange(val) {
      this.paginationForm.limit = val
      this.getMvs()
    },
    // mv
    async getMvs() {
      const { data: data } = await this.$axios.get('/mv/all', {
        params: {
          // 
          area: this.queryInfo.area,
          // 
          type: this.queryInfo.type,
          // 
          order: this.queryInfo.order,
          // 
          limit: this.paginationForm.limit,
          //  
          offset: this.paginationForm.page,
        },
      })
      if (data.code == 200) {
        this.mvlist = data.data
        //  total,
        if (data.count) {
          this.paginationForm.total = data.count
        }
      }
    },
  },
}
</script>

