<template>
  <div :class="['index-container', { 'padding-bottom': state }]">
    <div :class="['nav', { 'hide-nav': !hideNav }]">
      <ul :class="{ 'hide-nav': !hideNav }">
        <li>
          <router-link to="/discovery">
            <span class="iconfont icon-find-music"></span>
            
          </router-link>
        </li>
        <li>
          <router-link to="/playlists">
            <span class="iconfont icon-find-music"></span>
            
          </router-link>
        </li>
        <li>
          <router-link to="/songs">
            <span class="iconfont icon-music"></span>
            
          </router-link>
        </li>
        <li>
          <router-link to="/mvs">
            <span class="iconfont icon-mv"></span>
            MV
          </router-link>
        </li>
      </ul>
      <div class="author">
        <a href="https://www.bookbook.cc/views/About/author.html" target="blank">
          <el-tooltip class="item" effect="dark" placement="top">
            <div slot="content">
              <br />
              <br />
              WeChatP200048<br />
              QQ2845486124
            </div>
            <img src="~/assets/images/wx.png" alt="" />
          </el-tooltip>
        </a>
      </div>
    </div>
    <div class="main">
      <router-view></router-view>

      <el-backtop target=".main"></el-backtop>
      <Github class="github" />
    </div>
    <div :class="['player', { hide: !state }]">
      <aplayer
        ref="aplayer"
        :audio="audioInfo"
        :lrcType="1"
        :volume.sync="volume"
        :mini.sync="mini"
        :loop.sync="loop"
        :order.sync="order"
        :listFolded.sync="listFolded"
        :fixed="fixed"
        :autoplay="autoplay"
        @playing="play"
      />
    </div>
  </div>
</template>

<script>
// 
import { mapState } from 'vuex'

// 
import Github from '@/components/common/Github.vue'

export default {
  name: 'Index',
  components: {
    Github,
  },
  data() {
    return {
      // 
      // listMaxHeight: '130',
      volume: 0.7,
      // 
      mini: false,
      // , : 'all', 'one', 'none'
      loop: 'all',
      // , : 'list', 'random'
      order: 'random',
      // 
      autoplay: false,
      // 
      fixed: true,
      // 
      listFolded: true,
      state: true,
      //  nav
      hideNav: true,
    }
  },
  created() {
    this.$Bus.$on('switchState', state => {
      this.state = state
    })
    this.$Bus.$on('changeNavState', navState => {
      this.hideNav = navState
    })
  },
  mounted() {
    this.init()
    this.$Bus.$on('play', this.play)
    this.$Bus.$on('stop', this.stop)
    this.$Bus.$on('switch', this.switch)
    this.$Bus.$on('toggle', this.toggle)
  },
  computed: {
    // 3.vuex
    ...mapState(['audioInfo']),
  },
  methods: {
    // 
    init() {
      // 
      this.createClearBtn()

      // 
      let order = document.querySelector('#app > div.index-container.padding-bottom  div.aplayer-time > button.aplayer-icon.aplayer-icon-order')
      order.title = ''

      // 
      let loop = document.querySelector('#app  button.aplayer-icon.aplayer-icon-loop')
      loop.title = ''
    },
    // 
    play() {
      // console.log(this.$refs.aplayer.currentSettings)
      console.log('')
      let ap = this.$refs.aplayer
      setTimeout(() => {
        ap.play()
      }, 200)
    },
    // 
    stop() {
      console.log('')
      let ap = this.$refs.aplayer
      ap.pause()
    },
    // 
    switch() {
      console.log('')
      setTimeout(() => {
        let ap = this.$refs.aplayer
        ap.switch(0)
      }, 1000)
    },
    // 
    toggle() {
      let ap = this.$refs.aplayer
      ap.toggle()
    },
    // 
    remove(index) {
      // this.$refs.aplayer.audio.splice(1, 1)
      // this.$refs.aplayer.audio.splice(index, 1)
    },
    // 
    createClearBtn() {
      let customDiv = document.createElement('div')
      customDiv.title = ''
      customDiv.className = 'clearMusic iconfont icon-shanchu'
      let control = document.querySelector('.aplayer-time')
      control.appendChild(customDiv)
      customDiv.addEventListener('click', this.clear)
    },
    // 
    async clear() {
      this.$store.commit('clear')
      this.$refs.aplayer.pause()
      window.sessionStorage.removeItem('aplayer-setting')
      window.localStorage.removeItem('aplayer-setting')
      this.$message.success('')
    },
  },
}
</script>

<style scope>
/*   */
.main {
  overflow-anchor: none;
  position: relative;
}
.nav {
  position: relative;
}
.player {
  transition: all 0.3s;
  z-index: 999;
}
.hide {
  transform-origin: bottom;
  transform: rotateX(90deg);
}
.icon-shanchu:before {
  color: #f56c6c;
  position: relative;
  right: 5px;
}
.padding-bottom {
  padding-bottom: 66px;
}
.clearMusic {
  position: absolute;
  right: 100px;
  bottom: 27px;
  width: 20px;
  font-size: 18px;
  height: 20px;
}
.index-container .nav li a {
  white-space: nowrap;
}
.nav,
.nav ul {
  transform-origin: left;
  transition: all 0.3s;
}
.hide-nav {
  transform: rotateY(90deg);
  width: 0px !important;
}
.author img {
  position: absolute;
  bottom: 10px;
  width: 180px;
  left: 50%;
  transform: translateX(-50%);
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.github {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}
</style>
